  height: calc(100% - 50px);

  padding: 15px;
}

img[_v-df99c510] {
  width: 50%;
  height: auto;

  margin-left: 25%;
}

.modal-body[_v-df99c510] {
  max-height: calc(100vh - 60px - 155px - 10px); /* 60 for margin, 155 for modal header/footer and 10 for buffer */

