.custom-file-input[_v-df99c510]::-webkit-file-upload-button {
  visibility: hidden;
}

.custom-file-input[_v-df99c510] {
  background-color: white !important;
  color: white !important;

  display: inline-block;

  padding: 0px !important;
  margin: 0px !important;

  width: 120px;
  height: 36px;
  position: relative;
  top: 28px;
}

.custom-file-input[_v-df99c510]::before {
  content: attr(data-content);

  display: inline-block;

  padding: 7px;
  border-radius: 4px;

  background-image: -webkit-linear-gradient(top,#5cb85c 0,#419641 100%);
  background-image: -o-linear-gradient(top,#5cb85c 0,#419641 100%);
  background-image: -webkit-gradient(linear,left top,left bottom,from(#5cb85c),to(#419641));
  background-color: linear-gradient(to bottom,#5cb85c 0,#419641 100%);
  background-repeat: repeat-x;
  border-color: #419641;

  font-size: 14px;

  color: white;

  -webkit-user-select: none;

  cursor: pointer;

  width: 120px;
  margin-left: calc((100% - 120px)/2);

  text-align: center;
}
.custom-file-input[_v-df99c510]:hover::before {
  border-color: white;
}
.custom-file-input[_v-df99c510]:active::before {
  background: -webkit-linear-gradient(top, #e3e3e3, #f9f9f9);
}
.custom-file-input[_v-df99c510]:focus {
  outline: none;
}



